<template>
  <div id = 'edit_result'>
   <br>
    <div class="d-flex justify-content-center">
      <h3>Edit Result</h3>
    </div><br><br>
    <form role="form" @submit.prevent="updateResult">
      <div class="form-row align-items-center">
        <div class="form-group col-md-6">
          <label for="selectFirst">First Team Name</label>
          <select v-model="selected" class="form-control" required>
            <option value="" hidden>Select first team</option>
            <option v-for="team in first_team_name" v-bind:key="team.name" :disabled="team.name == sselected">{{team.name}}</option>
          </select>
        </div>
        <div class="form-group col-md-6">
          <label for="selectSecong">Second Team Name</label>
          <select v-model="sselected"  class="form-control" required>
            <option value="" hidden>Select second team</option>
            <option v-for="team in second_team_name" v-bind:key="team.name" :disabled="team.name == selected">{{team.name}}</option>
          </select>
        </div>
      </div>
      <div class="form-row align-items-center">
        <div class="form-group col-md-6">
          <label for="selectFirstScore">First Team Score</label>
          <input type="number" class="form-control" min="0" max="20" v-model="first_team_score" required>
        </div>
        <div class="form-group col-md-6">
          <label for="selectFirstScore">Second Team Score</label>
          <input type="number" class="form-control"  min="0" max="20" v-model="second_team_score" required>
        </div>
      </div>
      <div class="form-row align-items-center">
        <div class="form-group col-md-6">
          <label for="inputdate">Date</label>
          <input type="date" class="form-control" v-model="date" required>
        </div>
      </div>
      <div class="form-group">
        <button type="submit" class="btn btn-primary">Update</button>
        <router-link to='/' class="btn btn-danger">Cancel</router-link>
      </div>
    </form>
  </div>
</template>
<script>

import db from './firebaseInit'
export default {
  name: 'edit_result',
  data () {
    return {
      selected: null,
      sselected: null,
      first_team_score: null,
      second_team_score: null,
      date: null,
      first_team_name: [],
      second_team_name: []
    }
  },
  created () {
    db.collection('teams').get().then(querySnapshot => {
      querySnapshot.forEach(element => {
        const data = {
          'id': element.id,
          'name': element.data().name
        }
        this.first_team_name.push(data)
        this.second_team_name.push(data)
      })
    })
  },
  beforeRouteEnter (to, from, next) {
    db.collection("results").doc(to.params.id).get().then((doc) => {
      next(vm => {
        vm.first_team_score = doc.data().first_team_score
        vm.selected = doc.data().first_team_name
        vm.sselected = doc.data().second_team_name
        vm.second_team_score = doc.data().second_team_score
        vm.date = doc.data().date
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('results').doc(this.$route.params.id).get().then((doc) => {
        this.first_team_score = doc.data().first_team_score
        this.selected = doc.data().first_team_name
        this.sselected = doc.data().second_team_name
        this.second_team_score = doc.data().second_team_score
        this.date = doc.data().date
      })
    },
    updateResult () {
      db.collection('results').doc(this.$route.params.id).get().then((doc) => {
        doc.ref.update({
          first_team_score: this.selected,
          second_team_name: this.selected,
          first_team_score: this.first_team_score,
          second_team_score: this.second_team_score,
          date: this.date
        })
        .then(() => {
          this.$router.push('/')
        });
      })
    }
  }
}
</script>



