<template>
  <div id = 'league-table'>
    <br>
    <div class="float-right">
      <router-link to="/" class="btn btn-secondary">Back To Home</router-link>
    </div><br><br>
    <table class="table">
        <thead class="thead-light">
            <tr>
            <th scope="col">Pos</th>
            <th scope="col">Team</th>
            <th scope="col">Pld</th>
            <th scope="col">W</th>
            <th scope="col">L</th>
            <th scope="col">D</th>
            <th scope="col">Pts</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="data in league_datas" v-bind:key="data">
                <th scope="row">{{data.id}}</th>
                <td>{{data.team}}</td>
                <td>{{data.pld}}</td>
                <td>{{data.w}}</td>
                <td>{{data.l}}</td>
                <td>{{data.d}}</td>
                <td>{{data.pts}}</td>
            </tr>
        </tbody>
    </table>
    <!-- <p>
      <button @click="prevPage">Previous</button>
      <button @click="nextPage">Next</button>
    </p> -->
  </div>
</template>
<script>
import db from './firebaseInit'

export default {
  name: 'league-table',
  data () {
    return {
      league_datas: [],
      // pageSize:3,
      // currentPage:1
    }
  },
  created () {
    db.collection('teams').orderBy("id","asc").get().then(querySnapshot => {
      querySnapshot.forEach(element => {
        const value = {
          id:  element.data().id,
          'team': element.data().team,
          'pld': element.data().pld,
          'w': element.data().w,
          'l': element.data().l,
          'd': element.data().d,
          'pts': element.data().pts
        }
        this.league_datas.push(value);
        // // array list of team sorted by desc order
        // this.league_datas.sort((a,b)=> {
        //   return a-b;
        // })
      })
      console.log(this.league_datas)
     })
  }
}
</script>



